body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Gilroy';
  src: url('/Gilroy-ExtraBold.ttf') format('truetype');
}

.app {
  overflow-x: hidden;
  position: absolute;
  background-color: #212121;
  width: 100%;
  min-height: 100vh;
  min-width: 300px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

/* Messages */

.button {
  clear: both;
  float: left;
  font-weight: 700;
  position: relative;
  margin: 50px 0 0 0;
  border-radius: 20px 20px 20px 20px;
  background-color: #3544C9;
  color: white;
  padding: 20px 30px 20px 30px;
  font-size: 1.5rem;
}

.button:hover {
  background-color: #979797;
  cursor: pointer;
}

.message_text {
  font-weight: 400;
  position: relative;
  max-width: 300px;
  border-radius: 20px 20px 20px 20px;
  background-color: #3544C9;
  color: white;
  padding: 20px 20px 20px 20px;
  font-size: 1.2rem;
}

.message_image {
  max-width: 450px;
  max-height: 450px;
  width: 30vw;
  height: calc(30vw);
  margin: 10px 10px 0 0;
  border-radius: 10px 10px 10px 10px;
}

.message_video {
  margin: 10px 10px 0 0;
  border-radius: 10px 10px 10px 10px;
  background-color: #3544C9;
  padding: 20px;
  width: 40vw;
  height: calc(30vw / 4 * 3);
  min-width: 250px;
  min-height: calc(250px / 4 * 3);
}

.message_link {
  color: white;
}

.message {
  margin-top: 10px;
  clear: both;
  position: relative;
}

.message__incoming {
  float: left;
}

.message__outcoming {
  float: right;
}

.message__sender {
  margin: 30px 60px 10px 20px;
  font-weight: 300;
  position: relative;
  color: #EEEEEE;
  font-size: 0.8rem;
}

.message_content__incoming {
  border-radius: 0 20px 20px 20px !important;
}

.message_content__outcoming {
  border-radius: 20px 0 20px 20px !important;
}

/* Category */

.category {
  clear: both;
  font-family: 'Lato', sans-serif;
  width: 70vw;
  max-width: 1000px;
  position: relative;
  margin-right: 15vw;
  margin-left: 15vw;
  margin-top: 6%;
}

.category__title {
  padding-top: 100px;
  font-size: 2.3rem;
  color: white;
  font-weight: 700;
}

.category__subtitle {
  font-weight: 400;
  padding-top: 15px;
  font-size: 1.35rem;
  color: #BDBDBD;
}

/* Usecases */

.usecases {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Factoids */

.factoid_greed {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.factoid {
  position: relative;
  width: 230px;
  text-align: left;
}

.factoid__number {
  position: relative;
  font-weight: 700;
  font-size: 23rem;
  color: #3544C9;
  font-family: "Gilroy", sans-serif;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.factoid__description {
  position: relative;
  z-index: 2;
  width: 70vw;
  max-width: 230px;
  font-weight: 400;
  font-size: 1.3rem;
  color: white;
}

.factoid__picture {
  z-index: 3;
  position: absolute;
  max-width: calc(12rem);
  top: 60%;
  left: 60%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.last_thing {
  margin-bottom: 100px;
}

/* About device screen */

.about_device__flex {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.about_device__texts {
  margin-top: 50px;
  min-width: 320px;
  width: 30vw;
  font-family: 'Lato 300', sans-serif;
  color: white;
  font-size: 1.5rem;
}

.about_device__image {
  width: 330px;
}

.about_device__second_text {
  margin-top: 50px;
}

.about_device__second_text {
  margin-top: 50px;
}

/* Mail input */

input[type=text], select {
  clear: both;
  float: left;
  padding: 12px 20px;
  display: inline-block;
  border: 1px solid #979797;
  background: #f6f6f7;
  border-radius: 15px;
  box-sizing: border-box;
  margin-right: 15px;
}

.email_form {
  flex-wrap: wrap;
  clear: both;
  float: left;
  margin-top: 30px;
  display: flex;
  flex-direction: row;
}

/* Button link */

.button_link {
  clear: both;
  float: left;
  padding: 10px 0 5px 0;
  position: relative;
  font-weight: 200;
  font-size: 1.35rem;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
  text-underline-position: under;
  color: #EEEEEE;
  z-index: 5;
}

.button_link__top13 {
  top: 13vh;
}

.button_link__chosen {
  color: #3544C9 !important;
}

.button_link__margin {
  margin-right: 20px !important;
}

.button_link:hover {
  text-underline-position: under;
  color: #979797;
  cursor: pointer;
}

.button_link__fullpage {
  border-bottom: 1px #979797 dashed;
  margin-top: 50px;
  width: 70vw;
  max-width: 1000px;
}

.button_link__margin_left {
  margin-left: 15vw;
  margin-right: 15vw;
}

.button_link__grid {
  z-index: 3;
  display: flex;
  flex-flow: row wrap;
}


/* Intro screen */

.intro {
  margin-top: 3% !important;
  flex-direction: column;
  justify-content: space-around;
}

.intro__logo {
  max-width: 60vw;
}

.intro__langs_switch {
  display: flex;
}

.intro__logo_langs {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  vertical-align:middle;
  flex-direction: row;
  flex-wrap: wrap;
}

.intro__central {
  text-align: center;
  margin-top: 10%;
  color: white;
  font-weight: 400;
  font-size: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.intro_base__central {
  width: 40%;
  min-width: 270px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.intro_general__central {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.intro__text__first {
  font-size: 1.5rem;
  width: 70vw;
  max-width: 600px;
  margin-top: 20px;
  margin-bottom: 0px;
}

.intro__text__second {
  font-size: 1.5rem;
  width: 70vw;
  max-width: 600px;
  margin-top: 0px;
  margin-bottom: 20px;
}

.intro__image {
  width: 60vw;
  max-width: 400px;
}


/* For small screens (mobile) */

@media(max-width: 1000px) {
  .button {
    font-size: 1rem;
  }

  .about_device__flex {
    flex-direction: column;
    align-items: center;
  }

  .category {
    margin-left: 0px;
    margin-right: 0px;
    width: 80vw;
  }

  .about_device__texts {
    min-width: 0px;
    width: 100%;
    font-size: 1.3rem;
  }

  .factoid_greed {
    flex-direction: column;
    align-items: center;
  }

  .factoid__number {
    font-size: 13rem;
  }

  .factoid__picture {
    top: 40%;
    left: 50%;
    max-width: calc(10rem);
  }
}

@media(max-width: 500px) {
  .about_device__image {
    width: 60vw;
  }

  .category__subtitle {
    font-size: 1.2rem;
  }

  .category__title {
    font-size: 1.8rem;
  }

  .message_video {
    width: 70vw;
  }
}
